import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import { connect } from 'react-redux';
import { Area, AreaChart, YAxis, XAxis, Tooltip, ResponsiveContainer } from 'recharts';
import Typography from '@material-ui/core/Typography';
import axios from 'axios';
import { UPDATE_BUNDLE, UPDATE_DASH } from '../actions/types';

const data = [
  {name: 'Mon', uv: 4000, reach: 2400, amt: 2400},
  {name: 'Tue', uv: 3000, reach: 1398, amt: 2210},
  {name: 'Wed', uv: 2000, reach: 9800, amt: 2290},
  {name: 'Thu', uv: 2780, reach: 3908, amt: 2000},
  {name: 'Fri', uv: 1890, reach: 4800, amt: 2181},
  {name: 'Sat', uv: 2390, reach: 3800, amt: 2500},
  {name: 'Sun', uv: 3490, reach: 4300, amt: 2100},
];
const styles = theme => ({
  root: {
    flexGrow: 1,
    padding: theme.spacing.unit * 2
  },
  paper: {
    padding: theme.spacing.unit * 2,
    textAlign: 'center',
    height: 200,
    color: theme.palette.text.secondary,
    overflow: 'hidden'
  }
});

class Dashboard extends React.Component {
  componentDidMount() {
    if (this.props.selected !== 0) this.props.update();
    if(!localStorage.getItem('token-data')) {
      return;
    }
    axios.post('/web/bundle-check', { hashsum: this.props.auth.bundle.hashsum ,email: this.props.auth.user_email }, { headers: {'x-access-token': this.props.auth.user_token } })
      .then(response => {
        if(!response.data.error){
          if(response.data.bundle) {
            this.props.updateBundle({
              bundle: response.data.bundle
            });
          }
        }
      }
      ).catch((err) => {
        console.log(err);
      }
      ).then( () => {
        
      });
  }
  render() {
    const { classes } = this.props;
    return(
      <div className={classes.root}>
        <Grid container spacing={24}>
          <Grid item xs={12} sm={3} >
            <Paper className={classes.paper}>
              <Typography variant="title">New Registrations</Typography>
              
            </Paper>
          </Grid>
          <Grid item xs={12} sm={3}>
            <Paper style={{height:100}} className={classes.paper}>
              <Typography variant="title">New Registrations</Typography>
              <Typography variant="display3">500</Typography>
            </Paper>
          </Grid>
          <Grid item xs={12} sm={6}>
            <Paper style={{backgroundColor: '#417d95'}} className={classes.paper}>
              <ResponsiveContainer width="100%" height={180}>
                <AreaChart data={data}>
                  <defs>
                    <linearGradient id="colorUv" x1="0" y1="0" x2="0" y2="1">
                      <stop offset="5%" stopColor="#c1dee2" stopOpacity={0.8}/>
                      <stop offset="95%" stopColor="#c1dee2" stopOpacity={0}/>
                    </linearGradient>
                  </defs>
                  <XAxis dataKey="name" hide={true} />
                  <YAxis hide={true} />
                  {/* <CartesianGrid color="86AC41" strokeDasharray="3 3" /> */}
                  <Tooltip />
                  <Area type="monotone" dataKey="uv" stroke="#c1dee2" fillOpacity={1} fill="url(#colorUv)" />
                </AreaChart>
              </ResponsiveContainer>
              <Typography variant="caption" style={{color: '#fff'}}> Reach This Week </Typography>
            </Paper>
          </Grid>
          <Grid item xs={6} sm={3}>
            <Paper className={classes.paper}>xs=6 sm=3</Paper>
          </Grid>
          <Grid item xs={6} sm={3}>
            <Paper className={classes.paper}>xs=6 sm=3</Paper>
          </Grid>
          <Grid item xs={6} sm={3}>
            <Paper className={classes.paper}>xs=6 sm=3</Paper>
          </Grid>
          <Grid item xs={6} sm={3}>
            <Paper className={classes.paper}>xs=6 sm=3</Paper>
          </Grid>
        </Grid>
      </div>
    );
  }
}

Dashboard.propTypes = {
  classes: PropTypes.object.isRequired,
  selected: PropTypes.number.isRequired,
  update: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired,
  updateBundle: PropTypes.func.isRequired,
  updateDashboardData: PropTypes.func.isRequired
};
const mapStateToProps = (state) => {
  return { 
    selected: state.status.selected,
    auth: state.auth
  };
};
const mapDispatchToProps = (dispatch) => {
  return {
    update: () => {
      dispatch({
        type: 'UPDATE_ROUTE',
        payload: 0
      });
    },
    updateBundle: (bundle) => {
      dispatch({
        type: UPDATE_BUNDLE,
        payload: bundle
      });
    },
    updateDashboardData: (data) => {
      dispatch({
        type: UPDATE_DASH,
        payload: {data}
      });
    }
  };
};
export default connect(mapStateToProps, mapDispatchToProps) (withStyles(styles) (Dashboard));



